<template>
  <div id="map">
    <Modal v-if="game.count === states.length" :mode="mode" :reason="'win'" :score="game.score" />
    <div class="learn_notification" :class="animation ? 'animation' : ''" v-else-if="game.count !== states.length" @animationend="endAnime" @webkitAnimationEnd="endAnime" @msAnimationEnd="endAnime" @mozAnimationEnd="endAnime">
      {{ states[game.count] }}
    </div>
    <transition 
      v-on:enter="enter" 
      v-on:leave="leave"
      v-bind:css="false"
      appear>
      <v-map ref="map" :minZoom="2" :options="mapOptions" class="fullHeight">
        <v-geojson :geojson="geojson" :options="options.geojson"></v-geojson>
      </v-map>
  </transition>
  </div>  
</template>
<script>
import Vue2Leaflet from 'vue2-leaflet'
import Modal from '../modals/Modal'
import { TweenMax, Power4, TimelineMax } from 'gsap'

export default {
  data () {
    return {
      options: {
        geojson: {
          stroke: true,
          fill: true,
          strokeColor: '#000',
          color: '#000',
          fillColor: '#fff',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        }
      },
      states: [],
      game: {
        count: 0,
        attempts: 5,
      },
      layers: [],
      animation: false
    }
  },
  created () {
    for (let i = 0; i < this.geojson.length; i++) {
      this.states.push(this.geojson[i].properties.name)
    }
    this.states.sort(this.compareRandom)
  },
  mounted () {
    this.stopLoader()
  },
  props: {
    mode: {
      type: String,
      required: true
    },
    geojson: {
      required: true
    },
    mapOptions: {
      type: Object,
      required: true
    },
    stopLoader: {
      type: Function
    },
    latlng: {
      type: Number
    },
    country: {
      type: Boolean
    }
  },
  methods: {
    onEachFeature (feature, layer) {
      layer.bindPopup(layer.feature.properties.name)
      this.layers.push(layer)
      layer.on('click', () => {
        this.show(layer)
      }, this)
    },
    show (layer) {
      if (layer.feature.properties.name === this.states[this.game.count]) {
        this.animation = true
        this.game.count++
        this.game.attempts = 5
        layer.setStyle({fillColor: this.randomColor()})
        layer.off('click')
      } else {
        this.game.attempts--
        if (this.game.attempts === 0) {
          for (let i = 0; i < this.layers.length; i++) {
            if (this.layers[i].feature.properties.name === this.states[this.game.count]) {
              this.layers[i].setStyle({fillColor: '#9BFFE6'})
              if (!this.country) {
                this.$refs.map.mapObject.flyTo({
                  lat: ((this.layers[i]._bounds._northEast.lat + this.layers[i]._bounds._southWest.lat) / 2),
                  lng: ((this.layers[i]._bounds._northEast.lng + this.layers[i]._bounds._southWest.lng) /2)
                }, 6)
              }
              break
            }
          }
        }
      }
    },
    compareRandom () {
      return Math.random() - 0.5
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    },
    endAnime () {
      this.animation = false
    },
    enter(el, done) {
      const tl = new TimelineMax({
        onComplete: done
      })
      
      tl.set(el, {
        x: window.innerWidth * 1.5,        
        scale: 0.8,
        transformOrigin: '50% 50%'
      })
      
      tl.to(el, 0.5, {
        x: 0,
        ease: Power4.easeOut
      });
      
      tl.to(el, 1, {
        scale: 1,
        ease: Power4.easeOut
      });
    },
    leave(el, done) {
      TweenMax.fromTo(el, 1, {
        autoAlpha: 1
      }, {
        autoAlpha: 0,
        ease: Power4.easeOut,
        onComplete: done
      });
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Modal
  }
}
</script>
<style scoped>
@import "~leaflet/dist/leaflet.css";
#map {
  background-color: #185C69;
}
.learn_notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  min-height: 100px;
  display: flex;
  align-items: center;
  color: #404040;
  background-color: #9BFFE6;
  border-bottom: 5px solid rgba(0, 0, 0, 0.9);
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 1050;
}
.learn_notification {
  min-height: 50px;
  justify-content: center;
  font-size: 20px;
}
@media screen and (max-width: 400px) {
  .learn_notification {
    right: 0;
    top: 0;
    width: 100%;
  }
}
.animation {
  animation-name: next;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
}
.country {
  padding: 10px 10px 20px 10px;
  font-size: 20px;
}
.fullHeight {
  min-height: 100vh;
}
.leaflet-container {
  background-color: #A3CCFF;
}
@keyframes next {
  50% {
    transform: translateX(400px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
</style>


